import {getRandomArrayElement, getRandomPositiveInteger} from '../utils.js';
import {OFFERTYPE} from '../const.js';
import {destanition} from './destanition.js';

const points = [
  {
    'base_price': getRandomPositiveInteger(500, 850),
    'date_from': '2019-07-10T22:55:56.845Z',
    'date_to': '2019-07-11T11:22:13.375Z',
    'destination': destanition[getRandomArrayElement(destanition)].name,
    'id': '0',
    'is_favorite': Boolean(getRandomPositiveInteger(0, 1)),
    'offers': [getRandomArrayElement(OFFERTYPE), getRandomArrayElement(OFFERTYPE)],
    'type': getRandomArrayElement(OFFERTYPE)
  },

];
function getRandomPoints() {
  return getRandomArrayElement(points);
}

export {getRandomPoints};
